import React from 'react';
import { useState } from "react";
import { useParams } from "react-router-dom";
import { Nav, Button } from 'react-bootstrap'
import { useEffect } from "react";
import { addItem } from './store'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom';
import "./Detail.css";
import product_data from "./productData"

// type import
import {All} from "./all_data"
import {Product_data} from "./productData"


interface DetailProps {
    all: All[];
  }

function Detail(props: DetailProps): JSX.Element {

    let { id } = useParams();

    let [tap, setTap] = useState<number>(0);

    let [fade2, setFade2] = useState<string>('')
    useEffect(() => {
        window.scrollTo(0, 0)
        setFade2('end')
        return () => {
            setFade2('')
        }
    }, [])

    let selproduct = props.all.find((x) => x.id === Number(id));
    
    let dispatch = useDispatch();

    let [nogada] = useState<Product_data>(product_data);



    return (
        <div className={'container start ' + fade2}>

            <div className="D_ptoduct">
                <div className="D_productLeft">
                    <img src={selproduct?.imgurl} />
                </div>

                <div className="D_productRight">
                    <div className='D_brandbox'>
                        <img src="/types/img/detail_icon_hous.png" />
                        <p>{selproduct?.brand}</p>
                    </div>

                    <div className='D_titlebox'>
                        <p>{selproduct?.title}</p>
                        <img src="/types/img/detail_icon_share.png" />
                        <img src="/types/img/detail_icon_heart.png" />
                    </div>

                    <div className='D_price'>
                        <p>{selproduct?.persent}</p>
                        <p>{selproduct?.price + ''}</p>
                        <p>{selproduct?.saleprice + ''}</p>
                    </div>

                    <div className='D_benefit'>
                        <span>  </span>
                        <img src="/types/img/detail_icon_arrow.png"></img>
                    </div>

                    <div className='D_mileage'>
                        <img src="/types/img/detail_icon_M.png" />
                        <span><b> 3,30M</b> </span>
                    </div>

                    <div className='D_shipping'>
                        <div className='shipping_left'>
                            <img src="/types/img/detail_icon_car.png" />
                        </div>
                        <div className='shipping_right'>
                            <ul>
                                <li><img src="/types/img/detail_icon_LFshoong.png" />
                                    <span>  <b></b></span>
                                </li>
                                <li>
                                    <span className='shipping_day'>06/24()</span>
                                    <span className='shipping_probability'>  84%</span>
                                </li>
                            </ul>
                        </div>
                    </div>

                    <div className='D_LFcard'>
                        <img src="/types/img/detail_icon_LFcard.png" />
                        <span>LF PayLFmall   <b>5% </b></span>
                    </div>

                    <div className='D_LFpay'>
                        <img src="/types/img/detail_icon_card.png" />
                        <img src="/types/img/detail_icon_lfpay.png" />
                        <span>  20,000   3 /  </span>
                    </div>

                    <div className='D_map'>
                        <img src="/types/img/detail_icon_map.png" />
                        <span> </span>
                    </div>

                    <Button
                        variant="primary"
                        onClick={() => {
                            //  dispatch(addItem(  {id : 2,  imgurl : 'shoes1.jpg', name : 'Grey Yordan', count : 1}))

                            dispatch(
                                addItem({
                                    id: selproduct?.id!,
                                    imgurl: selproduct?.imgurl.replace("img/", ""),
                                    name: selproduct?.title!,
                                    count: 1,
                                })
                            );
                        }}
                        style={{ marginRight: "10px" }}
                    >
                        
                    </Button>

                    <Link to="/cart">
                        <Button variant="outline-success">   </Button>
                    </Link>
                </div>
            </div>



            <Nav variant="tabs" defaultActiveKey="link0">
                <Nav.Item>
                    <Nav.Link onClick={() => { setTap(0) }} eventKey="link0"></Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={() => { setTap(1) }} eventKey="link1">/</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={() => { setTap(2) }} eventKey="link2"></Nav.Link>
                </Nav.Item>
            </Nav>

            <TabContent tap={tap} nogada={nogada} />
        </div>
    )
}



function TabContent({ tap, nogada }:{tap: number; nogada:Product_data}):JSX.Element {
    let { id } = useParams();

    let [fade, setFade] = useState('')
    useEffect(() => {
        setTimeout(() => { setFade('end') }, 10)
        return () => {
            setFade('')
        }
    }, [tap])

    return (
        // 
        <div className={'start ' + fade}>
            {[<div className='detail_img'>
                <img src={nogada[Number(id)].imgurl0} />
                <img src={nogada[Number(id)].imgurl1} />
                <img src={nogada[Number(id)].imgurl2} />
                <img src={nogada[Number(id)].imgurl3} />
                <img src={nogada[Number(id)].imgurl4} />
            </div>,

        /// 
            <div className='detail_return'>
                <h3>/</h3>
                <table>
                    <tr>
                        <th>/</th>
                        <td>
                             - /  .<br />
                            <b>[/ ]</b>
                        </td>
                    </tr>
                    <tr>
                        <th>/ </th>
                        <td>
                                 7  .<br/>
                         ,           3  ( 30 ) .<br/>
                        </td>
                    </tr>
                    <tr>
                        <th>/ </th>
                        <td>
                        <img src="/types/img/table.JPG" /><br/>
                                ,        .<br/>
                         /    .<br/>
                                       .
                        </td>
                    </tr>
                    <tr>
                        <th>/ </th>
                        <td>
                           7  <br/>
                          TAG      <br/>
                          ,     <br/>
                              <br/>
                        </td>
                    </tr>
                    <tr>
                        <th></th>
                        <td>
                         :   4~5  <br/>
                           : 2~3    <br/>
                          ,     <br/>
                          : 2~3     <br/>
                          : 2~4  <br/>
                        </td>
                    </tr>
                </table>
            </div>,

        // 
            <div className='detail_shipping'>
                <h3></h3>
                <table>
                    <tr>
                        <th> </th>
                        <td>         .<br/>
                                   .
                        </td>
                    </tr>
                    <tr>
                        <th></th>
                        <td> </td>
                    </tr>
                </table>
            </div>][tap]}
        </div>
    )
}


export default Detail;